'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Alert, AlertDescription } from '@/components/ui/alert'
import type { Discussion } from '@/app/community/page'

interface DiscussionFormProps {
  onSubmit: (discussion: Omit<Discussion, 'id' | 'authorId' | 'authorName' | 'authorRole' | 'createdAt' | 'replies' | 'likes'>) => void
}

const categories = [
  'Crop Management',
  'Pest Control',
  'Soil Health',
  'Weather & Climate',
  'Market Prices',
  'Government Schemes',
  'Technology & Equipment',
  'Organic Farming',
  'General Discussion'
]

export default function DiscussionForm({ onSubmit }: DiscussionFormProps) {
  const [formData, setFormData] = useState({
    title: '',
    content: '',
    category: '',
    tags: ''
  })
  
  const [errors, setErrors] = useState<Record<string, string>>({})
  const [isSubmitting, setIsSubmitting] = useState(false)

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.title.trim()) {
      newErrors.title = 'Discussion title is required'
    } else if (formData.title.trim().length < 10) {
      newErrors.title = 'Title should be at least 10 characters long'
    }

    if (!formData.content.trim()) {
      newErrors.content = 'Discussion content is required'
    } else if (formData.content.trim().length < 20) {
      newErrors.content = 'Content should be at least 20 characters long'
    }

    if (!formData.category) {
      newErrors.category = 'Category is required'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) {
      return
    }

    setIsSubmitting(true)

    try {
      // Process tags
      const tags = formData.tags
        .split(',')
        .map(tag => tag.trim().toLowerCase())
        .filter(tag => tag.length > 0)
        .slice(0, 5) // Limit to 5 tags

      const discussionData = {
        title: formData.title.trim(),
        content: formData.content.trim(),
        category: formData.category,
        tags
      }

      onSubmit(discussionData)
      
      // Reset form
      setFormData({
        title: '',
        content: '',
        category: '',
        tags: ''
      })
    } catch (error) {
      console.error('Error submitting discussion:', error)
      setErrors({ submit: 'Failed to create discussion. Please try again.' })
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }))
    }
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {errors.submit && (
        <Alert variant="destructive">
          <AlertDescription>{errors.submit}</AlertDescription>
        </Alert>
      )}

      {/* Discussion Title */}
      <div>
        <Label htmlFor="title">Discussion Title *</Label>
        <Input
          id="title"
          value={formData.title}
          onChange={(e) => handleInputChange('title', e.target.value)}
          placeholder="e.g., Best practices for organic tomato farming?"
          className={errors.title ? 'border-red-500' : ''}
        />
        {errors.title && (
          <p className="text-sm text-red-500 mt-1">{errors.title}</p>
        )}
        <p className="text-sm text-muted-foreground mt-1">
          {formData.title.length}/100 characters
        </p>
      </div>

      {/* Category */}
      <div>
        <Label htmlFor="category">Category *</Label>
        <Select value={formData.category} onValueChange={(value) => handleInputChange('category', value)}>
          <SelectTrigger className={errors.category ? 'border-red-500' : ''}>
            <SelectValue placeholder="Select a category" />
          </SelectTrigger>
          <SelectContent>
            {categories.map((category) => (
              <SelectItem key={category} value={category}>
                {category}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
        {errors.category && (
          <p className="text-sm text-red-500 mt-1">{errors.category}</p>
        )}
      </div>

      {/* Discussion Content */}
      <div>
        <Label htmlFor="content">Discussion Content *</Label>
        <Textarea
          id="content"
          value={formData.content}
          onChange={(e) => handleInputChange('content', e.target.value)}
          placeholder="Describe your question, share your experience, or start a discussion topic. Be specific and provide context to help others understand and respond effectively."
          rows={6}
          className={errors.content ? 'border-red-500' : ''}
        />
        {errors.content && (
          <p className="text-sm text-red-500 mt-1">{errors.content}</p>
        )}
        <p className="text-sm text-muted-foreground mt-1">
          {formData.content.length}/1000 characters
        </p>
      </div>

      {/* Tags */}
      <div>
        <Label htmlFor="tags">Tags (Optional)</Label>
        <Input
          id="tags"
          value={formData.tags}
          onChange={(e) => handleInputChange('tags', e.target.value)}
          placeholder="e.g., organic, tomato, farming, tips (separate with commas)"
        />
        <p className="text-sm text-muted-foreground mt-1">
          Add up to 5 tags to help others find your discussion. Separate tags with commas.
        </p>
      </div>

      {/* Guidelines */}
      <div className="bg-muted/30 rounded-lg p-4">
        <h4 className="font-medium text-foreground mb-2">Community Guidelines</h4>
        <ul className="text-sm text-muted-foreground space-y-1">
          <li>• Be respectful and constructive in your discussions</li>
          <li>• Provide specific details to help others understand your situation</li>
          <li>• Search existing discussions before posting similar questions</li>
          <li>• Use appropriate categories and tags for better visibility</li>
          <li>• Share your knowledge and experiences to help fellow farmers</li>
        </ul>
      </div>

      {/* Submit Button */}
      <div className="flex justify-end space-x-4">
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? 'Creating Discussion...' : 'Start Discussion'}
        </Button>
      </div>
    </form>
  )
}
