'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { useUser } from '@/context/UserContext'
import type { Discussion } from '@/app/community/page'

interface DiscussionCardProps {
  discussion: Discussion
  onLike: (discussionId: string) => void
}

export default function DiscussionCard({ discussion, onLike }: DiscussionCardProps) {
  const { user, isAuthenticated } = useUser()

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffInHours = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60))
    
    if (diffInHours < 1) {
      return 'Just now'
    } else if (diffInHours < 24) {
      return `${diffInHours}h ago`
    } else if (diffInHours < 168) {
      return `${Math.floor(diffInHours / 24)}d ago`
    } else {
      return date.toLocaleDateString('en-IN', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      })
    }
  }

  const getRoleColor = (role: string) => {
    switch (role) {
      case 'expert':
        return 'bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200'
      case 'farmer':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
      case 'buyer':
        return 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200'
      case 'seller':
        return 'bg-orange-100 text-orange-800 dark:bg-orange-900 dark:text-orange-200'
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200'
    }
  }

  const handleReply = () => {
    if (!isAuthenticated) {
      alert('Please login to reply to discussions')
      return
    }
    // In a real app, this would open a reply form or navigate to discussion detail
    alert(`Opening reply form for: ${discussion.title}`)
  }

  const handleLike = () => {
    onLike(discussion.id)
  }

  return (
    <Card className="hover:shadow-md transition-shadow duration-300">
      <CardHeader className="pb-3">
        <div className="flex items-start justify-between mb-3">
          <div className="flex items-center space-x-3">
            <div className="flex items-center space-x-2">
              <span className="font-medium text-foreground">{discussion.authorName}</span>
              <Badge className={`text-xs ${getRoleColor(discussion.authorRole)}`}>
                {discussion.authorRole}
              </Badge>
            </div>
          </div>
          <div className="flex items-center space-x-4 text-sm text-muted-foreground">
            <span>{formatDate(discussion.createdAt)}</span>
            <Badge variant="outline" className="text-xs">
              {discussion.category}
            </Badge>
          </div>
        </div>

        <CardTitle className="text-xl mb-2 hover:text-primary cursor-pointer">
          {discussion.title}
        </CardTitle>

        <CardDescription className="text-base leading-relaxed">
          {discussion.content}
        </CardDescription>
      </CardHeader>

      <CardContent>
        {/* Tags */}
        {discussion.tags.length > 0 && (
          <div className="flex flex-wrap gap-2 mb-4">
            {discussion.tags.map((tag) => (
              <Badge key={tag} variant="secondary" className="text-xs">
                #{tag}
              </Badge>
            ))}
          </div>
        )}

        {/* Actions */}
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <Button
              variant="ghost"
              size="sm"
              onClick={handleLike}
              className="text-muted-foreground hover:text-primary"
            >
              👍 {discussion.likes}
            </Button>
            
            <Button
              variant="ghost"
              size="sm"
              onClick={handleReply}
              className="text-muted-foreground hover:text-primary"
            >
              💬 {discussion.replies} replies
            </Button>
          </div>

          <div className="flex items-center space-x-2">
            <Button
              variant="outline"
              size="sm"
              onClick={handleReply}
            >
              Reply
            </Button>
            
            <Button
              variant="ghost"
              size="sm"
              className="text-muted-foreground hover:text-primary"
            >
              Share
            </Button>
          </div>
        </div>

        {/* User's own post indicator */}
        {user?.id === discussion.authorId && (
          <div className="mt-3 pt-3 border-t border-border">
            <Badge variant="outline" className="text-xs">
              Your Discussion
            </Badge>
          </div>
        )}
      </CardContent>
    </Card>
  )
}
