'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { useUser } from '@/context/UserContext'
import DiscussionCard from '@/components/community/DiscussionCard'
import DiscussionForm from '@/components/community/DiscussionForm'
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

export interface Discussion {
  id: string
  title: string
  content: string
  category: string
  authorId: string
  authorName: string
  authorRole: string
  createdAt: string
  replies: number
  likes: number
  tags: string[]
}

const categories = [
  'All Categories',
  'Crop Management',
  'Pest Control',
  'Soil Health',
  'Weather & Climate',
  'Market Prices',
  'Government Schemes',
  'Technology & Equipment',
  'Organic Farming',
  'General Discussion'
]

// Mock data for demonstration
const mockDiscussions: Discussion[] = [
  {
    id: '1',
    title: 'Best practices for organic tomato farming?',
    content: 'I am planning to start organic tomato farming this season. Can experienced farmers share their best practices for soil preparation, pest management, and yield optimization?',
    category: 'Organic Farming',
    authorId: 'farmer1',
    authorName: 'Rajesh Kumar',
    authorRole: 'farmer',
    createdAt: '2024-01-15T10:30:00Z',
    replies: 12,
    likes: 8,
    tags: ['organic', 'tomato', 'farming', 'tips']
  },
  {
    id: '2',
    title: 'Current wheat prices in Punjab market',
    content: 'What are the current wheat prices in Punjab mandis? Planning to sell my harvest next week and want to know the best time and place.',
    category: 'Market Prices',
    authorId: 'farmer2',
    authorName: 'Priya Sharma',
    authorRole: 'farmer',
    createdAt: '2024-01-14T15:45:00Z',
    replies: 6,
    likes: 4,
    tags: ['wheat', 'prices', 'punjab', 'market']
  },
  {
    id: '3',
    title: 'Effective natural pest control methods',
    content: 'Looking for natural and eco-friendly pest control methods that work well for vegetable crops. Chemical pesticides are affecting soil health.',
    category: 'Pest Control',
    authorId: 'expert1',
    authorName: 'Dr. Amit Patel',
    authorRole: 'expert',
    createdAt: '2024-01-13T09:20:00Z',
    replies: 18,
    likes: 15,
    tags: ['pest-control', 'natural', 'organic', 'vegetables']
  },
  {
    id: '4',
    title: 'PM-KISAN scheme application process',
    content: 'Can someone guide me through the PM-KISAN scheme application process? What documents are required and how long does it take for approval?',
    category: 'Government Schemes',
    authorId: 'farmer3',
    authorName: 'Sunita Devi',
    authorRole: 'farmer',
    createdAt: '2024-01-12T14:10:00Z',
    replies: 9,
    likes: 7,
    tags: ['pm-kisan', 'government', 'scheme', 'application']
  }
]

export default function CommunityPage() {
  const { user, isAuthenticated } = useUser()
  const [discussions, setDiscussions] = useState<Discussion[]>(mockDiscussions)
  const [filteredDiscussions, setFilteredDiscussions] = useState<Discussion[]>(mockDiscussions)
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('All Categories')
  const [isNewDiscussionOpen, setIsNewDiscussionOpen] = useState(false)

  // Filter discussions based on search and category
  useEffect(() => {
    let filtered = discussions

    if (searchTerm) {
      filtered = filtered.filter(discussion =>
        discussion.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        discussion.content.toLowerCase().includes(searchTerm.toLowerCase()) ||
        discussion.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()))
      )
    }

    if (selectedCategory !== 'All Categories') {
      filtered = filtered.filter(discussion => discussion.category === selectedCategory)
    }

    // Sort by creation date (newest first)
    filtered.sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())

    setFilteredDiscussions(filtered)
  }, [discussions, searchTerm, selectedCategory])

  const handleNewDiscussion = (newDiscussion: Omit<Discussion, 'id' | 'authorId' | 'authorName' | 'authorRole' | 'createdAt' | 'replies' | 'likes'>) => {
    if (!user) return

    const discussion: Discussion = {
      ...newDiscussion,
      id: Date.now().toString(),
      authorId: user.id,
      authorName: user.name,
      authorRole: user.role,
      createdAt: new Date().toISOString(),
      replies: 0,
      likes: 0
    }

    setDiscussions(prev => [discussion, ...prev])
    setIsNewDiscussionOpen(false)
  }

  const handleLike = (discussionId: string) => {
    if (!isAuthenticated) {
      alert('Please login to like discussions')
      return
    }

    setDiscussions(prev =>
      prev.map(discussion =>
        discussion.id === discussionId
          ? { ...discussion, likes: discussion.likes + 1 }
          : discussion
      )
    )
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <div className="bg-gradient-to-r from-blue-50 to-purple-50 dark:from-blue-950 dark:to-purple-950 py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-4xl font-bold text-foreground mb-4">
              Community Discussions
            </h1>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              Connect with fellow farmers, share experiences, ask questions, and learn from the community
            </p>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Search and Filter Bar */}
        <div className="flex flex-col md:flex-row gap-4 mb-8">
          <div className="flex-1">
            <Input
              placeholder="Search discussions, topics, or tags..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full"
            />
          </div>
          
          <Select value={selectedCategory} onValueChange={setSelectedCategory}>
            <SelectTrigger className="w-full md:w-48">
              <SelectValue placeholder="Select category" />
            </SelectTrigger>
            <SelectContent>
              {categories.map((category) => (
                <SelectItem key={category} value={category}>
                  {category}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          {isAuthenticated && (
            <Dialog open={isNewDiscussionOpen} onOpenChange={setIsNewDiscussionOpen}>
              <DialogTrigger asChild>
                <Button className="w-full md:w-auto">
                  Start Discussion
                </Button>
              </DialogTrigger>
              <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
                <DialogHeader>
                  <DialogTitle>Start New Discussion</DialogTitle>
                </DialogHeader>
                <DiscussionForm onSubmit={handleNewDiscussion} />
              </DialogContent>
            </Dialog>
          )}
        </div>

        {/* Results Summary */}
        <div className="mb-6">
          <p className="text-muted-foreground">
            Showing {filteredDiscussions.length} of {discussions.length} discussions
            {selectedCategory !== 'All Categories' && ` in ${selectedCategory}`}
            {searchTerm && ` matching "${searchTerm}"`}
          </p>
        </div>

        {/* Discussions List */}
        {filteredDiscussions.length > 0 ? (
          <div className="space-y-6">
            {filteredDiscussions.map((discussion) => (
              <DiscussionCard 
                key={discussion.id} 
                discussion={discussion} 
                onLike={handleLike}
              />
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <div className="text-6xl mb-4">💬</div>
            <h3 className="text-xl font-semibold text-foreground mb-2">
              No discussions found
            </h3>
            <p className="text-muted-foreground mb-6">
              {searchTerm || selectedCategory !== 'All Categories'
                ? 'Try adjusting your search or filter criteria'
                : 'Be the first to start a discussion in the community'}
            </p>
            {isAuthenticated && (
              <Button onClick={() => setIsNewDiscussionOpen(true)}>
                Start First Discussion
              </Button>
            )}
          </div>
        )}

        {/* Call to Action for Non-authenticated Users */}
        {!isAuthenticated && (
          <div className="mt-16 text-center bg-muted/30 rounded-lg p-8">
            <h3 className="text-2xl font-semibold text-foreground mb-4">
              Join the Community
            </h3>
            <p className="text-muted-foreground mb-6">
              Register to participate in discussions, ask questions, and share your knowledge with fellow farmers
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button asChild>
                <a href="/auth/register">Join Community</a>
              </Button>
              <Button variant="outline" asChild>
                <a href="/auth/login">Login</a>
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
